<template>
  <div class="upload">
    <form id="upload" enctype="multipart/form-data" method="post">
      <input type="file" name="file" id="file" accept=".md" @input="flie" />
      <input type="button" value="提交" v-on:click="uploadPic();" disabled='true' />
    </form>
  </div>
</template>
<script>
export default {
  name: "upload",
  methods: {
    uploadPic() {
      var form = document.getElementById("upload"),
        formData = new FormData(form);
      $.ajax({
        url: `http://${window.location.hostname}:9090/api/upload`,
        type: "post",
        data: formData,
        processData: false,
        contentType: false,
        success: function(res) {
          if (res) {
            alert("上传成功！");
          }
          console.log(res);
        },
        error: function(err) {
          alert("上传失败", err);
        }
      });
    },
    flie(){
        if($('input[name="file"]')[0].files[0].name.split('.').slice(-1)[0] == 'md'){
            $('input[type="button"]')[0].disabled = false
        }else{
            console.log('不符合文件规范')
            $('input[type="button"]')[0].disabled = true
        }
    }
  },
};
</script>
<style lang="css">
.upload {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 100%;
}
#upload {
  display: flex;
  flex-direction: column;
  width: 200px;
  background: rgba(112, 255, 56, 0.651);
  padding: 30px;
  border-radius: 15px;
}
input:nth-child(1){
    margin-bottom: 30px;
    padding: 5px;
    border-radius: 10px;
    background: rgb(55, 182, 51);
}
</style>

